.nav-container {
  background-color: rgb(2, 2, 46);
  padding: 2rem 4rem;
  display: flex;
  /* justify-content: space-between; */
  font-size: 18px;
  color: white;
  align-items: center;
}

@media (max-width: 768px) {
  .nav-container {
    padding: 1rem 2rem;
  }
}

.nav-container h2{
  padding-right: 5rem;
  margin: 0px;
}

@media (max-width: 768px) {
  .nav-container h2{
    font-size: large;
    padding-right: 1.4rem;
  }
}

@media (max-width: 375px) {
  .nav-container {
    font-size: 15px;
    padding-right: 1rem;
  }
}

@media (max-width: 320px) {
  .nav-container {
    font-size: 12px;
    padding-right: 0.8rem;
  }
}

.nav-container span{
  font-size: 25px;
  font-weight: bold;
  color:#D4AF37;
}

@media (max-width: 375px) {
  .nav-container span{
    font-size: 15px;
  }
}

header{
  display: flex;
  align-items: center;
}

header h2{
  padding-right: 100px;
}

header a{
  text-decoration: none;
  color: white;
  padding-right: 15px;
}

header a:hover{
  color: #D4AF37;
  transition: 0.15s;
}

.home-section, 
.skills-section,
.about-section,
.project-section{
  padding: 4rem;
}

.home-section,
.about-section,
.project-section{
  background-color: rgb(229, 229, 247);
}


@media (max-width: 1024px) and (max-width: 768px) {
  .home-section,
  .skills-section,
  .about-section,
  .project-section {
      padding: 2rem 2rem;
    }
}

.personal-info h1{
  margin: 0;
  font-size: 4.5rem;
  color: navy;
  font-weight: bolder;
}

@media (max-width: 1024px) {
  .personal-info h1 {
      font-size: 4rem;
    }
}

@media (max-width: 768px) {
  .personal-info h1 {
      font-size: 3rem;
    }
}

.personal-info h2{
  font-size: 35px;
  font-weight: normal;
  margin: 0px;
}

@media (max-width: 768px) {
  .personal-info h2 {
    font-size: 28px;
    margin: 5px;
  }
}

.personal-info p{
  font-size: larger;
}

.media-link{
  display: flex;
}

.media-link a{
  padding-right: 15px;
  text-decoration: none;
  text-align: left;
}

.media-link a img{
  height: 30px;
  background-color: navy;
  padding: 5px;
  border-radius: 3px;

}

.resume {
    height: 30px;
    background-color: navy;
    padding: 5px;
    border-radius: 3px;
    color: white;
    font-weight: bold;
    text-align: center;
    font-size: 20px;
    padding-left: 10px;
}

.home-section,
.about-section{
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
}

@media (max-width: 450px) {
.home-section {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 425px) {
.personal-info {
    padding-top: 2rem;
  }
}

.personal-info-img img{
  max-width: 100%;
  align-items: center;
  border: 1px solid none;
  box-sizing: border-box;
}

@media (max-width: 1024px) {
  .personal-info-img img {
    height: 350px;
  }
}

@media (max-width: 768px) {
  .personal-info-img img {
    height: 260px;
  }
}

@media (max-width: 700px) {
  .personal-info-img,
  .personal-info-img img {
    display: none;
    visibility: hidden;
  }
}


/* SKILLS */

.skills-section h1{
  color: navy;
}

.skill-title hr{
  margin-bottom: 50px;
}

span img{
  height: 80px;
  margin-bottom: 20px;
}

.skill-info{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 4rem;
}

@media (max-width: 768px) {
  .skill-info {
