    gap: 2rem;
  }
}

@media (max-width: 425px) {
  .skill-info {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
}

.info-grid h4{
  font-size: 20px;
  margin: 0px;
}

/* About Section */

.about-section{
