  box-shadow: 0px 0px 6px gray;
  border-radius: 50%;
}


@media (max-width: 1024px) {
  .about-info img {
    /* height: 400px; */
  }
}

@media (max-width: 768px) {
  .about-info img {
    /* height: 320px; */
  }
}

@media (max-width: 375px) {
  .about-info img {
      /* height: 300px; */
    }
}

@media (max-width: 320px) {
  .about-info img {
      height: 250px;
    }
}

.about-title h2{
  color: navy;
}

.about-info p{
  font-size: larger;
  line-height: 40px;
  text-align: justify;
}

@media (max-width: 1024px) {
  .about-info p {
    font-size: large;
    line-height: 30px;
  }
}

@media (max-width: 768px) {
  .about-info p {
    font-size: 16px;
    line-height: 20px;
  }
}

/* Project Css */

.project-section h1 {
  color: navy;
}

.project-title hr {
  margin-bottom: 50px;
}

.project-info{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 4rem;
}

@media (max-width: 1024px) {
  .project-info {
    grid-template-columns: 1fr 1fr;
  }
}

@media (max-width: 768px) {
  .project-info {
    grid-template-columns: 1fr;
  }
}
.project-details{
    display: block;
    border-radius: 5px;
    box-sizing: border-box;
    box-shadow: 0px 0px 6px gray;
}

.project-details img{
    width: 100%;
    border-radius: 2px 2px 0 0;
}

.project-detail{
  padding: 2rem;
}

.project-detail h4{
  font-size: larger;
  margin: 0px;
}

.project-detail p{
  line-height: 25px;
}

.project-link {
  display: flex;
}

.project-link a {
  padding-right: 25px;
}

.project-link a img {
  display: flex;
  height: 30px;
  background-color:navy;
  padding: 5px;
  border-radius: 3px;
  display: flex;
}

/* Contact Section */

/* Default styles for larger screens */
.contact-section {
  margin: auto;
  width: 50%;
  padding: 4rem 4rem;
}

.contact-section .form-section{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.contact{
  text-align: center;
  margin-bottom: 2rem;
}

.contact h1{
  color: white;
  text-transform: uppercase;
  background-color: navy;
  width: 50%;
  margin: auto;
  border-radius: 15px;
  padding: 8px;
}

@media (max-width: 768px) {
.contact-section {
    width: 80%;
    padding: 10px 0px;
  }
  }
@media (max-width: 480px) {
.contact {
    width: 100%;
    padding: 10px 0px;
  }
  }

.contact p{
  line-height: 25px;
  font-size: large;
}

.contact-address{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.contact-address p{
  font-size: large;
  text-align: center;
  margin: 10px;
}

.contact-address svg{
  color: navy;
}

/* Note here */

.form-control {
  margin-bottom: 20px;
}

/* Style all input fields to take up full width */

.form-control input[type='text'],
.form-control input[type='email'],
.form-control textarea[type='text'] {
  width: 97%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-shadow: 0px 0px 6px gray;
  outline: none;
}

.form-control textarea[type='text']{
  -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
  width: 100%;
}

/* Media query for screens with a maximum width of 768px (e.g., tablets) */
@media (max-width: 768px) {

  /* Adjust padding and font size for smaller screens */
  .form-control input[type='text'],
  .form-control input[type='email'] {
    padding: 8px;
    font-size: 14px;
  }
}

/* Media query for screens with a maximum width of 480px (e.g., mobile phones) */
@media (max-width: 480px) {

  /* Further adjust padding and font size for smaller screens */
  .form-control input[type='text'],
  .form-control input[type='email']{
    padding: 6px;
    font-size: 12px;
  }
}

@media (max-width: 1024px) {
  .form-control textarea[type='text'] {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-shadow: 0px 0px 6px gray;
    outline: none;
    
  }
}

/* Note */
.form {
  width: 100%;
}

.form-control {
  display: flex;
  flex-direction: column;
}

/* Media query for screens with a maximum width of 768px (e.g., tablets) */
@media (max-width: 768px) {
  .contact-section {
    flex-direction: column;
    /* Stack items vertically on smaller screens */
    padding: 20px;
  }

  .form {
    width: 100%;
    /* Take up full width on smaller screens */
  }
}

/* Media query for screens with a maximum width of 480px (e.g., mobile phones) */
@media (max-width: 480px) {
  .form-control {
    margin-bottom: 10px;
    /* Reduce margin on smaller screens */
  }

  .contact-section {
    padding: 10px;
  }
}

button{
  background-color: navy;
  margin: auto;
  width: 30%;
  display: flex;
  color: white;
  justify-content: center;
  font-size: 18px;
  text-transform: uppercase;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  box-shadow: 0px 0px 6px gray;
  cursor: pointer;
}

svg{
  padding-right: 5px;
}

@media (max-width: 480px) {
  button {
      width: 40%;
      font-size: 15px;
    }
}

@media (max-width: 320px) {
  button {
      font-size: 12px;
    }
}

/* Footer Section */

.footer-section{
  padding-top: 8rem;
}

.footer{
  background-color: navy;
  color: white;
  padding: 0.5rem;
}

.footer p{
  text-align: center;
}